/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464d53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042, 1.14%, #ffa739, 100.75%);
  --appColor: #3c3f45;
  --one: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
}

.App {
  /* background-image: url(data:image/png;base64,); */
  mix-blend-mode: overlay;
  background-color: #000;
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}
.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}
.btn {
  background-color: rgba(100, 200, 00, 0.9);
  padding: 0.5rem;
  font-weight: bold;
  /* opacity: 0.6; */
  transition: 300ms all;
  border: 4px solid transparent;
}
.btn:hover {
  background-color: #fff;
  /* opacity: 1; */
  cursor: pointer;
}
.header {
  display: flex;
  gap: 5rem;
  font-weight: bold;
  font-size: 3rem;
  justify-content: center;
  color: #fff;
  text-transform: uppercase;
  font-style: italic;
}
.blur {
  background-color: rgba(253, 220, 43, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(180px);
  z-index: -9;
}
@media screen and (max-width: 768px) {
  .header {
    flex-direction: column;
    gap: 1rem;
    font-size: x-large;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
  }
}
